@import url("https://fonts.googleapis.com/css?family=Nunito:600,900");

.dropdown-list li {
  list-style: none;
  margin: 0 0 0.5em 0;
  padding: 0.3em;
  width: 100%;
  text-align: center;
}

.dropdown-list:last-child {
  margin: 0;
}

.active {
  color: white;
  background-color: black;
  padding: 0.2em;
}

.dropdown-list li a {
  color: black;
  text-decoration: none;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.04em;
}

.dropdown-list {
  position: absolute;
  right: 3em;
  padding-left: 0;
  width: 11em;
  background-color: rgba(198, 121, 83, 0.5) !important;
}

.dropdown-button {
  right: 0;
  top: 0.5%;
  position: absolute;
  color: black !important;
  line-height: 38px;
  cursor: pointer;
}

.dropdown-menu {
  letter-spacing: 0.2em;
  font-weight: 900;
}

.title {
  letter-spacing: 0.2em;
  line-height: 42px;
  margin: 1.5em;
  color: black;
  text-decoration: none;
  font-weight: 800;
}

.fa-angle-down {
  margin-left: 10px;
}

@media screen and (max-width: 1024px) {
  .title {
    line-height: 32px;
  }

  .dropdown-button {
    line-height: 20px;
  }

  .dropdown-list {
    right: 2em;
    width: 9em;
    top: 2em;
  }

  .dropdown-list li a {
    font-size: 11px;
  }
}

@media screen and (max-width: 420px) {
  .title {
    font-size: 0.7em;
  }

  .dropdown-button {
    line-height: 25px;
    font-size: 0.7em;
  }
  .dropdown-list {
    top: 2em;
    right: 2em;
  }
}

@media screen and (max-width: 568px) and (orientation: landscape) {
  .dropdown-button {
    top: 2%;
  }
  .dropdown-list {
    right: 2em;
    top: 2em;
  }
}

@media screen and (max-width: 812px) and (orientation: landscape) {
  .dropdown-button {
    top: 1.5%;
  }

  .dropdown-list {
    right: 2em;
    top: 2em;
  }
}
